@import "../../Assets/scss/App.scss";
@import "../../Assets/scss/Typography.scss";
@import "../../Assets/scss/Colors.scss";
@import "../../Assets/scss/_breakpoints.scss";

.skills-section {
  background-color: $color-background;


  .heading-behind {
    height: 6rem;
    color: $color-background-variant;
    @media screen and (max-width: $width-sm) {
      height: 2rem;
    }
  }
  .custom-shape-divider-bottom-16070489245 .shape-fill {
    fill: $color-background;
  }
}
.sub-heading {
  font-size: 2.2rem !important;
  font-weight: 500 !important;
  padding: 1em 0rem 0.5em 0em;
  font-family: $font-Rubik;
  color: $color-secondary;
}

$host: "https://s3-us-west-2.amazonaws.com/s.cdpn.io/397014/";

$card-width: 180px;
$card-width-sm: 94vw;
$card-width-md: 45vw;
$card-width-lg: 180px;
$card-height: 160px;

$thumb-height: 120px;

$border-radius: 3px;
$box-shadow: 0 1px 4px rgba(black, 0.3);
$transition: cubic-bezier(0.17, 0.67, 0.5, 1.03);
$timing-1: 0.4s 0.15s;
$timing-2: 0.5s 0.25s;

@mixin pos($pos, $left: null, $top: null, $right: null, $bottom: null) {
  position: $pos;
  left: $left;
  @if $top {
    top: $top;
  }
  @if $left {
    left: $left;
  }
  @if $right {
    right: $right;
  }
  @if $bottom {
    bottom: $bottom;
  }
}

@mixin size($width, $height) {
  width: $width;
  height: $height;
}

// @media screen and (max-width: 599px) {
//   .skill-card {
//     width: $card-width-sm;
//     background-color: yellow !important;
//   }
// }
.skill-card {
  background-color: lighten($color-background-variant, 5%);
  text-align: center !important;
  @include size($card-width, $card-height);
  border-radius: $border-radius;
  box-shadow: $box-shadow;
  overflow: hidden;
  display: inline-block;

  margin-right: 20px;
  margin-bottom: 20px;
  align-items: center !important;

  @media screen and (max-width: $width-sm) {
    width: $card-width-sm;
    // background-color: yellow;
  }

  @media screen and (min-width: $width-md) {
    width: $card-width-md;
    // background-color: red ;
  }
  @media screen and (min-width: $width-lg) {
    width: $card-width-lg;
    // background-color: green;
  }
  .thumb {
    @include size(auto, $thumb-height);
 
    border-radius: $border-radius;

    img {
      background-repeat: no-repeat;
      @include size(auto, 100px);
      padding: 10px;
    }
  }
  .infos {
    @include size(auto, $card-height);
    position: relative;
    padding: 4px 4px;
    background-color: lighten($color-background-variant, 10%);
    transition: $timing-1 $transition;

    .category {
      margin: 0.5em 0em;
      text-transform: uppercase;
      font-size: 0.75rem;
      color: $color-secondary;
      font-family: $font-Rubik;
    }
    .title {
      position: relative;
      color: $color-primary;
      font-family: $font-Rubik;
      font-size: 1.5rem;
      transition: $timing-2 $transition;
      padding-top: 4px;
      padding-bottom: 5px;
      border-bottom: 3px solid lighten($color-background-variant, 5%);

    }
    .image {
      // @include pos(absolute, $bottom: 0);
      position:absolute;
      bottom: -15px;
      transform: translateY(-50%);
      @include size(auto, 60px);
      background-size: 100% auto;
      display: inline-block;
      opacity: 0;
      margin-left: 15px;
      // margin-right: 20px;

      // color: $color-primary !important;
      // padding-top: 20px;

      // background-repeat: no-repeat;
      // @include size(auto, 100px);
      // padding: 10px;

    }
    .date,
    .seats {
      margin-bottom: 10px;
      text-transform: uppercase;
      font-size: 0.85rem;
      color: $color-primary;
    }
    .seats {
      display: inline-block;
      margin-bottom: 24px;
      padding-bottom: 24px;
      border-bottom: 1px solid $color-primary;
      opacity: 0;
      transition: $timing-2 $transition;
    }
    .detail {
      font-family: $font-Blinker;
      line-height: 1.5;
      font-size: 1.2rem;
      color: $color-on-surface;
      // opacity:30;
      transition: $timing-2 $transition;
      font-weight: 300;
      // text-align: left;
    }
    .txt {
      // font-family: $merriweather;
      line-height: 2;
      font-size: 0.95rem;
      color: $color-primary;
      opacity: 0;
      transition: $timing-2 $transition;
    }
    .details {
      position: absolute;
      bottom: 10px !important;
      margin: 10px 0;
      letter-spacing: 1px;
      color: $color-primary;
      font-size: 0.9rem;
      text-transform: uppercase;
      cursor: pointer;
      opacity: 0;
      transition: $timing-2 $transition;
      a[href] {
        position: relative;
        &,
        &:link,
        &:visited,
        &:active {
          text-decoration: none;
          padding-bottom: 3px;
          font-weight: bold;
        }
        &::after {
          content: "";
          background: $color-primary;
          @include size(0, 1px);
          transition: 0.35s $transition;
        }
        &:hover::after {
          width: 100%;
          box-shadow: $box-shadow;
        }
      }
    }
  }
  .infos:hover {
    transform: translateY(-$thumb-height);
    .seats,
    .txt,
    .details {
      opacity: 1;
    }
  }
  &:hover .infos {
    transform: translateY(-$thumb-height);
    .seats,
    .txt,
    .details {
      opacity: 1;
    }
    .image {
      opacity: 1;
      transition: $timing-2 $transition;
    }
    .title {
      position: relative;
      color: $color-primary;
      font-family: $font-Rubik;
      font-size: 1.5rem;
      transition: $timing-2 $transition;
      // text-align: left;
    }
  }
}

