body {
  font-family: Arial, Helvetica, sans-serif;
}

/* Float four columns side by side */
// .column {
//   float: left;
//   width: 50%;
//   padding: 0 10px;
//   min-height: 500px;
// }

/* Remove extra left and right margins, due to padding */
.row {
  margin: 0 -5px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
    min-height: 400px;
  }
}

