$host: "https://s3-us-west-2.amazonaws.com/s.cdpn.io/397014/";
$card-width: 380px;
$card-width-sm: 94vw;
$card-width-md: 45vw;
$card-width-lg: 380px;
$card-height: 350px;

$border-radius: 3px;
$box-shadow: 0 1px 4px rgba(black, 0.3);
// $transition: cubic-bezier(0.17, 0.67, 0.5, 1.03);
// $timing-1: 0.4s 0.15s;
// $timing-2: 0.5s 0.25s;

@mixin size($width, $height) {
  width: $width;
  height: $height;
}

